/*

If we use Google fonts, host them locally for improved performance. Instruction and a wizard to generate the relevant settings can be found here: https://google-webfonts-helper.herokuapp.com/fonts

*/
/* Font weights */
/* REM Font sizing - http://blog.divshot.com/post/29552945105/useful-sass-mixins-for-responsive-design-font-sizing */
html {
  font-size: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

body {
  color: #4E514E;
  font-size: 14px;
  line-height: 20px;
  font-size: 0.875rem;
  line-height: 1.25rem; }
  @media only screen and (min-width: 48em) {
    body {
      font-size: 16px;
      line-height: 24px;
      font-size: 1rem;
      line-height: 1.5rem; } }

h1, h2, h3, h4, h5, h6 {
  margin: 0 0 1.5rem;
  font-size: 24px;
  line-height: 28px;
  font-size: 1.5rem;
  line-height: 1.75rem;
  font-weight: 700; }

h1 {
  font-size: 28px;
  line-height: 32px;
  font-size: 1.75rem;
  line-height: 2rem; }
  @media only screen and (min-width: 48em) {
    h1 {
      font-size: 40px;
      line-height: 40px;
      font-size: 2.5rem;
      line-height: 2.5rem; } }

h2 {
  font-size: 22px;
  line-height: 26.2px;
  font-size: 1.375rem;
  line-height: 1.6375rem; }
  @media only screen and (min-width: 48em) {
    h2 {
      font-size: 30px;
      line-height: 31px;
      font-size: 1.875rem;
      line-height: 1.9375rem; } }

h3,
h4,
h5,
h6 {
  font-size: 20px;
  line-height: 24px;
  font-size: 1.25rem;
  line-height: 1.5rem; }
  @media only screen and (min-width: 48em) {
    h3,
    h4,
    h5,
    h6 {
      font-size: 24px;
      line-height: 28px;
      font-size: 1.5rem;
      line-height: 1.75rem; } }

p {
  margin: 0 0 1.5rem; }

a {
  transition: all 0.3s ease 0s;
  font-weight: 700;
  color: #4E514E; }
  a:hover, a:active, a:focus {
    color: #4E514E; }

strong,
b {
  font-weight: 700; }

.medium {
  font-size: 16px;
  line-height: 22px;
  font-size: 1rem;
  line-height: 1.375rem; }

.small {
  font-size: 12px;
  line-height: 20px;
  font-size: 0.75rem;
  line-height: 1.25rem; }

/*

LAYOUT
======

Top level layout - should only contain a couple of bits and pieces for <HTML>, <body> and perhaps some main wrappers. 

Grid structure for this project uses flexbox and the grid layout is applied in the scss for each element rather than using grid classes in the HTML. 

*/
html {
  overflow-x: hidden;
  min-height: 100vh; }

body {
  padding: 0;
  margin: 0;
  background-color: #fff;
  color: #4E514E;
  max-width: 64em; }
